.modal-container{
    background-color: rgba(10, 10, 10, 0.5);
    width: 100vw;
    height: 100vh;
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    display: flex;
    align-items: center;
    justify-content: center;
}

.modal-content{
    display: inline-block;
    background-color: #fff;
    position: relative;
    padding: 2rem;
    border-radius: .5rem;
}

.close-modal{
    background-color: #FF5050;
    position: absolute;
    top: .5rem;
    right: .5rem;
    font-size: 1rem;
    font-weight: bold;
    color: #fff;
    padding: .5rem;
    border-radius: .5rem;
    cursor: pointer;
}