.search{
    width:30%;
    height: 1.5rem;
    border-radius: 5px;
    border: 1px solid #222;
    text-align: center;
    font-weight: bold;
    background-image: url('../../assets/icons/search.svg');
    background-repeat: no-repeat;
    background-position: 4% 50%;
}

.search-container{
    padding-top: 1.5rem;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 1rem;
}

.search-btn{
    width: 5rem;
    font-weight: bold;
    background-color: #FF5050;
    color: #FFF;
    border: none;
    padding: .25rem;
    border-radius: 4px;
}

.search-btn:hover{
    background-color: #FF9595;
    color: #222;
    box-shadow: 0 0 5px #FF9595, 0 0 25px #FF9595, 0 0 50px #FF9595, 0 0 200px #FF9595;
}

.search-btn:active{
    transform: scale(0.9);
}